.survey-form-container {
    display: flex;
    flex-direction: column;
    height: 100vh;
  }
  
  .header-bar {
    background-color: #28f321; /* Blue color */
    padding: 10px;
    text-align: center;
    width: 100%;
  }
  
  .header-title {
    margin: 0;
    color: #085905; /* White color */
  }
  
  .content {
    flex-grow: 1;
    padding: 20px;
  }
  
  button {
    margin-top: 10px;
  }
  
  label {
    display: block;
    margin-bottom: 5px;
  }